// Получаем только цифры из номера телефона
export const getCleanPhoneNumber = (phone: string): string => {
  return phone.replace(/\D/g, '');
};

// Форматируем номер для отображения (добавляем + в начало если его нет)
export const formatPhoneForDisplay = (phone: string): string => {
  if (!phone) return '';
  const cleanPhone = getCleanPhoneNumber(phone);
  return cleanPhone.startsWith('+') ? phone : `+${cleanPhone}`;
};

